import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController,
} from "ionic-angular";
import { QuoteColumnViewModel } from "../../Model/ViewModel/QuoteColumnViewModel";

/**
 * Generated class for the QuoteColumnModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page quote-column-add-edit-modal",
  templateUrl: "quote-column-add-edit-modal.html"
})
export class QuoteColumnAddEditModalPage {
  item: QuoteColumnViewModel;
  mode: string;
  title: string;
  CanEditBatch: boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController,

  ) {
    this.item = new QuoteColumnViewModel();
    this.item.schemaname=navParams.data.item.schemaname;
this.item.tablename=navParams.data.item.tablename;
this.item.columnname=navParams.data.item.columnname;
this.item.creator=navParams.data.item.creator;
this.item.create_time=navParams.data.item.create_time;
this.item.modifier=navParams.data.item.modifier;
this.item.last_update_time=navParams.data.item.last_update_time;

    this.mode=navParams.data.mode;
    this.CanEditBatch = navParams.data.CanEditBatch;
    if (this.mode === "POST") this.title = "新增";
    else this.title = "更新";
  }
  SelectColumn() {
    let modal = this.modalCtrl.create("CheckCodeColumnSelectModalPage", {
      item: this.item,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
       this.item.columnname = select_data.columnname;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad QuoteColumnAddEditModalPage");
  }
}



